import React from "react";
import s from "./TeamMember.module.css";

const TeamMember = ({ image, name, status }) => {
  return (
    <>
      <section className={s.TeamMember}>
        <div className={s.avatar}>{image}</div>
        <h2>
          {name} / <span>{status}</span>
        </h2>
      </section>
    </>
  );
};

export default TeamMember;
