import React from "react";
import { Link } from "react-router-dom";
import logo from "./Header-image/LOGO.png";
import phone from "./Header-image/Iphone.png";
import phoneFull from "./Header-image/IphoneFull.png";
import vector from "./Header-image/Vector.png";
import scrollDown from "./Header-image/scroll-down.png";
import bollSmall from "./Header-image/Ball-small.png";
import bollLarge from "./Header-image/Ball-large.png";
import iconSocial from "./Header-image/icon-Social.png";
import iconNft from "./Header-image/icon-NFT.png";
import iconSolana from "./Header-image/icon-Solana.png";
import groopIcons from "./Header-image/Group-icons.png";
import "./Header-style.css";
import "./Adaptive/style1000.css";
import "./Adaptive/style768.css";
import "./Adaptive/style480.css";
import "./Adaptive/style360.css";
import Button from "../Buttons/Button";

const HeaderComponent = ({ background, setsmallMenu, smallMenu }) => {
  const [down, setScrollDown] = React.useState(false);
  window.down = down;

  const scrollToBottom = () => {
    window.scrollTo({
      top: document.documentElement.scrollHeight,
      behavior: "auto",
    });
  };

  const openMenu = () => {
    if (!smallMenu) {
      setsmallMenu(true);
    } else if (smallMenu) {
      setsmallMenu(false);
    }
  };

  React.useEffect(() => {
    const scrollDown = () => {
      if (window.pageYOffset > 300) {
        setScrollDown(true);
      } else if (window.pageYOffset < 300 && window.pageYOffset > 200) {
        setScrollDown(false);
      }
    };
    window.removeEventListener("scroll", scrollDown);
    window.addEventListener("scroll", scrollDown, { passive: true });
    return () => window.removeEventListener("scroll", scrollDown);
  }, []);

  return (
    <>
      <section style={background}>
        <div className={`scroll-parents ${down && "bottom"}`}>
          <div className={`scroll ${down ? "scroll-icon" : null}`}>
            {!down ? (
              <img
                onClick={scrollToBottom}
                src={scrollDown}
                alt="scroll-down"
              />
            ) : (
              <a href="#">
                <img src={scrollDown} alt="scroll-down" />
              </a>
            )}
          </div>
        </div>
        <header>
          <section className="for-all-header">
            <div className="header-start">
              <div className="header-image">
                <img src={logo} alt="Logo" />
              </div>
              <nav>
                <div className="navBar-menu">
                  <div>
                    <a href="#game-concept">GAME CONCEPT</a>
                  </div>
                  <div>
                    <a href="#nft-collection">NFT COLLECTION</a>
                  </div>
                  <div>
                    <a href="#road-map">ROAD MAP</a>
                  </div>
                  <div>
                    <Link to="marketplace">MARKETPLACE</Link>
                  </div>
                  <div>
                    <Link className="whitepaper" to="whitepaper">
                      Whitepaper
                    </Link>
                  </div>
                  <div onClick={openMenu} className="small-menu">
                    <span className={`${smallMenu && "chaildOne"}`}></span>
                    <span></span>
                    <span className={`${smallMenu && "chaildThree"}`}></span>
                  </div>
                </div>
              </nav>
            </div>
            <section className="header-content">
              <img src={bollSmall} alt="planet" className="bollSmall" />
              <img src={bollLarge} alt="planet" className="bollLarge" />
              <div className="h1-content">
                <h2>WEB</h2>
                <h2>3.0</h2>
                <h2>MOBILE</h2>
                <h2>GAME</h2>
                <span className="china-text"></span>
                <h1 className="h1">APP FOR PLAY, EARN, COMMUNICATE</h1>
                <div className="forButton">
                  <div className="buttonAndButton">
                    <Button />
                    <a href="#">Get a free NFT</a>
                    <span>Buy NFT</span>
                    <div>
                      <img src={vector} alt="vector" />
                    </div>
                  </div>
                </div>
              </div>
              <div className="header-phone">
                <img src={phoneFull} alt="Phone" />
                <img src={phone} alt="Phone" />
              </div>
              <div className="scroll-down"></div>
            </section>
            <div className="header-footer">
              <div className="header-footer-social">
                <div>
                  <img className="icon" src={iconSocial} />
                </div>
                <div className="header-footer-social-description">
                  <h2>Social-Fi Element</h2>
                  <p>The app helps you make new friends</p>
                </div>
              </div>
              <div className="header-footer-nft">
                <div className="absolutle-icons">
                  <img className="icon" src={groopIcons} alt="icons" />
                </div>

                <div>
                  <img className="icon" src={iconNft} />
                </div>
                <div className="header-footer-nft-description">
                  <h2>NFT Collection</h2>
                  <p>
                    We were inspired by our love for our pets when creating the
                    collection
                  </p>
                </div>
              </div>
              <div className="header-footer-solana">
                <div>
                  <img src={iconSolana} />
                </div>
                <div className="header-footer-solana-description">
                  <h2>Solana network </h2>
                  <p>Game mechanics are based on blockchain technology</p>
                </div>
              </div>
            </div>
          </section>
        </header>
      </section>
    </>
  );
};

export default HeaderComponent;
