import "./WebSite.css";
import HeaderComponent from "./Header/Header";
import GameConcept from "./GameConcept/GameConcept";
import GameCharacters from "./Game-Characters/Game-Characters";
import NftCollection from "./Nft-Collection/NftCollection";
import NftCardContent from "./Nft-Cards-Content/NftCardsContent";
import RoadMap from "./Road-map/RoadMap";
import OurTeam from "./Our-Team/OurTeam";
import BeforeFooter from "./Before-footer/BeforeFooter";
import Footer from ".//Footer/Footer";
import React from "react";
import SmallMenu from "./Small Menu/Small-menu";

function WebSite() {
  const width = {
    maxWidth: "1280px",
    padding: "0 40px",
    margin: "0 auto",
    change: () => {
      if (window.innerWidth < 651) {
        width.padding = "0 20px";
      }
    },
  };
  const widthForSectionThree = { maxWidth: "1280px", margin: "0 auto" };
  const paragraf = {
    position: "relative",
    zIndex: "5",
    fontFamily: "'Play', sans-serif",
    fontSize: "18px",
    LineHeight: "23px",
    change: () => {
      if (window.innerWidth <= 1000 && window.innerWidth > 480) {
        paragraf.fontSize = "16px";
      } else if (window.innerWidth <= 480) {
        paragraf.fontSize = "14px";
      }
    },
  };

  paragraf.change();
  width.change();

  const background = {
    position: "relative",
    background: "#1B1A1A",
  };

  const [smallMenu, setsmallMenu] = React.useState(false);

  return (
    <section style={{ overflow: "hidden", position: "relative" }}>
      <SmallMenu smallMenu={smallMenu} setsmallMenu={setsmallMenu} />
      <HeaderComponent
        smallMenu={smallMenu}
        setsmallMenu={setsmallMenu}
        background={background}
      />
      <GameConcept background={background} width={width} paragraf={paragraf} />
      <GameCharacters
        background={background}
        width={widthForSectionThree}
        paragraf={paragraf}
      />
      <NftCollection background={background} width={width} />
      <NftCardContent background={background} />
      <RoadMap background={background} />
      <OurTeam background={background} />
      <BeforeFooter />
      <Footer width={width} />
    </section>
  );
}

export default WebSite;
