import React from "react";
import s from "./Characters.module.css";
import groopCats from "./Charaters-image/Group-cats.png";
import iconSpeed from "./Charaters-image/icon-speed.png";
import groopDogs from "./Charaters-image/Group-dogs.png";
import iconPower from "./Charaters-image/icon-power.png";
import Button from "../Buttons/Button";
import "./Characters-style.css";
import BuyNft from "../Buttons/BuyNft";

const GameCharacters = ({ background, width, paragraf }) => {
  return (
    <section style={background}>
      <section className={s.section3} style={width}>
        <div className={s.sectionHeader}>
          <h2>
            <span>GAME</span> CHARATERS
          </h2>
        </div>
        <div className={s.descriptionAndCats}>
          <div className={s.description}>
            <h2>Abyssinian Cat</h2>
            <div className={s.speed}>
              <img src={iconSpeed} alt="speed" />
              <span>Increased speed</span>
            </div>
            <p style={paragraf}>
              Dressed in a special suit that allows it to better perform the
              function of delivering special capsules to the energy stations.
            </p>
            <div className={s.button}>
              <Button />
            </div>
          </div>
          <div className={s.groopCats}>
            <img src={groopCats} alt="cats" />
          </div>
        </div>
        <div className={s.descriptionAndDogs}>
          <div>
            <img src={groopDogs} alt="dogs" />
          </div>
          <div className={s.descriptionTwo}>
            <h2>Shiba Inu Dog</h2>
            <div className={s.speed}>
              <img src={iconPower} alt="speed" />
              <span style={{ color: "#F15641" }}>Increased power</span>
            </div>
            <p style={paragraf}>
              Dressed in a special suit that allows it to better perform the
              function of delivering special capsules to the energy stations.
            </p>
            <div className={s.buyNft}>
              <BuyNft />
            </div>
          </div>
        </div>
      </section>
    </section>
  );
};

export default GameCharacters;
