import React from "react";
import s from "./OurTeam.module.css";
import TeamMember from "./Team-Member/TeamMember";
import avaMax from "./OurTeam-image/AvaMax.png";
import avaLev from "./OurTeam-image/AvaLev.png";
import avatar from "./OurTeam-image/avatar.png";

const OurTeam = ({ background }) => {
  return (
    <section style={background}>
      <section className={s.sectionOurTeam}>
        <h2>
          OUR <span>TEAM</span>
        </h2>
        <div className={s.aboutTeamMembers}>
          <div className={s.content}>
            <TeamMember
              image={<img src={avaMax} alt="avatar" />}
              name={"MAX"}
              status={"CDO"}
            />
            <TeamMember
              image={<img src={avatar} alt="avatar" />}
              name={"XXX"}
              status={"CFO"}
            />
            <TeamMember
              image={<img src={avaLev} alt="avatar" />}
              name={"LEV"}
              status={"CEO"}
            />
          </div>
        </div>
      </section>
    </section>
  );
};

export default OurTeam;
