import React from "react";
import NftCard from "../Nft-Card/NftCard";
import CatCommon from "../Nft-Card/NftCard-Image/CatCommon.png";
import CatUncommon from "../Nft-Card/NftCard-Image/catUncommon.png";
import CatEpic from "../Nft-Card/NftCard-Image/catEpic.png";
import dogCommon from "../Nft-Card/NftCard-Image/dogCommon.png";
import s from "./NftContent.module.css";

const NftCardContent = ({ background }) => {
  return (
    <section className={s.sectionNftContent} style={background}>
      <section className={s.nftCardContent}>
        <div className={s.scrollX}>
          <NftCard image={CatCommon} />
          <NftCard image={CatUncommon} />
          <NftCard image={CatEpic} />
          <NftCard image={dogCommon} />
          <NftCard image={CatCommon} />
          <NftCard image={CatUncommon} />
          <NftCard image={CatEpic} />
        </div>
        <div className={s.scrollX2}>
          <NftCard image={CatCommon} />
          <NftCard image={CatUncommon} />
          <NftCard image={CatEpic} />
          <NftCard image={dogCommon} />
          <NftCard image={CatCommon} />
          <NftCard image={CatUncommon} />
          <NftCard image={CatEpic} />
        </div>
      </section>
    </section>
  );
};

export default NftCardContent;
