import React from "react";
import s from "./NftCard.module.css";
import line from "./NftCard-Image/Line 538.png";
import vector from "./NftCard-Image/Vector.png";

const NftCard = ({ image, rarity }) => {
  return (
    <>
      <div className={s.card}>
        <div className={s.cardImg}>
          <img src={image} alt="NFT" />
        </div>
        <div className={s.nameAndRar}>
          <h2>Shiba Inu Dog #3457</h2>
          <span>Epic</span>
        </div>
        <div>
          <img src={line} alt="line" />
        </div>
        <div className={s.lvlAndMint}>
          <h3>Lvl: 0</h3>
          <h4>Mint: 0/3</h4>
        </div>
        <div className={s.solanaPriceAndBuy}>
          <img src={vector} alt="solana" />
          <h2>22.84</h2>
          <button>Buy now</button>
        </div>
      </div>
    </>
  );
};

export default NftCard;
