import React from "react";
import s from "./Collection.module.css";
import NftCard from "../Nft-Card/NftCard";

const NftCollection = ({ background, width }) => {
  return (
    <section id="nft-collection" className={s.nftCollection} style={background}>
      <section className={s.section} style={width}>
        <div className={s.banner}>
          <h2>
            <span>NFT</span> COLLECTION
          </h2>
        </div>
      </section>
    </section>
  );
};

export default NftCollection;
