import React from "react";
import s from "./BeforeFooter.module.css";
import fone from "./BeforeFooter-image/image 269.png";
import Button from "../Buttons/Button";

const BeforeFooter = () => {
  return (
    <section className={s.BeforeFooter}>
      <img className={s.fone} src={fone} alt="fone" />
      <div className={s.eventContent}>
        <h2>Get in on event !</h2>
        <p>Complete tasks, get POS tokens and</p>
        <p>
          receive NFT pet for <span>free</span>
        </p>
        <Button />
      </div>
    </section>
  );
};

export default BeforeFooter;
